import { Component, Input } from '@angular/core';

@Component({
  selector: 'tab',
  styles: [`
    .pane {
      padding: 1em;
    }
  `],
  template: `
    <div [hidden]="!active" class="pane">
    </div>
  `
})
export class Tab {
  @Input('tabTitle') title: string;
  @Input() active = false;
}



